import React from 'react';
import {inject, observer} from 'mobx-react';

import '../Style/Filter.css';

let Filter = inject('Store')(
    observer(
        class FilterButton extends React.Component{
            checks=()=>{
                let types = this.props.Store.typesOfPokemons;
                let div = [];
                for(let i = 0; i<types.length; i++){
                    div[i] = <div>
                        <input type="checkbox" value={types[i]} id={types[i]} className = 'pokemon-type-checkbox'/>
                        <label for={types[i]}>{types[i]}</label> 

                    </div>
                }
                return div;
            }

            filterPokemons=()=>{
                this.props.Store.get_allpokemons();
                this.props.Store.filter = [];
                let types = this.props.Store.typesOfPokemons;
                for(let i = 0; i<types.length; i++){
                    if(document.getElementById(types[i]).checked === true){
                        this.props.Store.filter[this.props.Store.filter.length] = types[i];
                        console.log(types[i]);
                    }
                }
                document.getElementById('filter').style.display='none';

                let tempPokemons = [];
                for(let i = 0; i<this.props.Store.allPokemons.length; i++){
                    // console.log("inFor");
                    let tempPoke = this.props.Store.allPokemons[i];
                    let isCorrect = false;
                    // console.log(tempPoke);
                    for(let j = 0; j<tempPoke[3].length; j++){
                        let tempType = tempPoke[3][j].type.name;
                        for(let f = 0; f<this.props.Store.filter.length; f++){
                            let filtredType = this.props.Store.filter[f];
                            console.log(tempType+"/"+filtredType);
                            if(tempType === filtredType){
                                isCorrect = true;
                            }
                        }
                    }
                    if(isCorrect === true){
                        tempPokemons.push(tempPoke);
                    }
                }
                this.props.Store.change_allPokemons(tempPokemons);
                let temp = this.props.Store.countPokemonOnPage;
                this.props.Store.change_countPokemonOnPage(20);
                this.props.Store.change_countPokemonOnPage(temp);
            }
            render(){
                let check = this.checks();
                return(
                    <div className = 'filter-block'>
                        <div className='modal-filter-window' id='filter'>
                            <div id="close-block" onClick={()=>{document.getElementById('filter').style.display='none'}}>
                                <span id="x">X</span>
                            </div>
                            <div className='many-checks'>
                                {check}
                            </div> 
                            <div className = 'filter-buttons'>
                                <div className = 'button search' onClick={this.filterPokemons}>Search</div>
                                <div className = 'button cancel' onClick={()=>{document.getElementById('filter').style.display='none'}}>Cancel</div>
                            </div>
                        </div>   
                        <div className='filter' onClick = {()=>{document.getElementById('filter').style.display='block'}}>Filter</div>             
                    </div>
                );
            }
        }
    )
)

export default Filter;