import { decorate, observable, action} from "mobx";

class Store{
    allPokemons = require('./poke.json');
    selectedPokemon = [];
    countPokemonOnPage = 10;
    clickedPage = 1;
    clickedPokemon = 1;
    typesOfPokemons = ['bug', 'dark', 'dragon', 'electric', 'fairy', 'fighting', 'fire', 'flying', 'ghosts',
    'grass', 'ground', 'ice', 'normal', 'poison', 'psychic', 'rock', 'steel', 'water']
    filter = [];

    requestRoot = "https://pokeapi.co/api/v2/pokemon/";

    change_allPokemons(new_){
        this.allPokemons = new_;
    }
    change_selectedPokemon(pokemon){
        this.selectedPokemon = pokemon;
    }
    change_countPokemonOnPage(new_count){
        this.countPokemonOnPage = new_count;
    }
    change_clickedPage(num){
        this.clickedPage = num;
    }
    change_clickedPokemon(index){
        this.clickedPokemon = index;
    }

    get_allpokemons(){
        this.allPokemons = require('./poke.json');
    }
    getPokemon(num){
        return this.allPokemons[num];
    }

    addValuesToFilter(value){
        this.filter.push(value);
    }
    refreshFilter(){
        this.filter = [];
    }
    removeValuesFromFilter(value){
        for( var i = 0; i < this.filter.length; i++){ 
            if ( this.filter[i] === 5) {
              this.filter.splice(i, 1); 
            }
         }
    }
}

decorate(Store, {
    allPokemons: observable,
    selectedPokemon: observable,
    countPokemonOnPage: observable,
    clickedPage: observable,
    clickedPokemon: observable,
    filter: observable,

    change_allPokemons: action,
    change_selectedPokemon: action,
    change_countPokemonOnPage: action,
    change_clickedPage:action,
    change_clickedPokemon: action,
    get_allpokemons:action,
    getPokemon: action,
    addValuesToFilter: action,
    refreshFilter: action
});

let Pok = new Store();

export default Pok;
export {Store};