import React from 'react';
import { inject, observer } from 'mobx-react';
import '../Style/Pokemon.css';

let Pokemon = inject('Store')(
    observer(
        class PokeObject extends React.Component { 
            getManyPokemons = (count, clickedPage) => {
                if (this.props.Store.allPokemons)
                    console.log(clickedPage - 1);
                let Pokemons = [];
                for (let i = 0; i < count; i++) {
                    Pokemons[i] = this.props.Store.getPokemon((((clickedPage - 1) * count) + i));
                }
                return Pokemons;
            }
            convertToBlock = (many_pokemons) => {
                if (!many_pokemons.length)
                    return <div>No one pokemons!!!</div>;
                let Div = [];
                many_pokemons.map((item, index) => {
                    Div.push(<div className='pokemon' onClick={() => this.modalWindow((this.props.Store.clickedPage - 1) * this.props.Store.countPokemonOnPage + index + 1)}>
                        <img src={(item !== 0 && item !== undefined) ? item[0] : ""} /><br />
                        <p>{(item !== 0 && item !== undefined) ? item[1] : ""}</p>
                    </div>);
                })
                return Div;
            }
            modalWindow = (index) => {
                console.log(index);
                this.props.Store.change_clickedPokemon(index);
                document.getElementById('modalElem').style.display = 'block';
                document.getElementById('poki').style.display='none';
            }
            render() {
                let pokemonsOnPage = this.convertToBlock(this.getManyPokemons(this.props.Store.countPokemonOnPage, this.props.Store.clickedPage));
                let pokemon = this.props.Store.getPokemon(this.props.Store.clickedPokemon - 1);
                return (
                    <div>
                        <div className='modalWindow modal' id='modalElem'>
                            <div id="close-block" onClick={() => { 
                                document.getElementById('modalElem').style.display = 'none';
                                document.getElementById('poki').style.display = 'grid';
                                }}>
                                <span id="x">X</span>
                            </div>
                            <div className='grid-container'>
                                <div className='nameOfPokemon'>{(pokemon!==undefined)?pokemon[1]:""}</div>
                                <div className='photo'><img src={(pokemon!==undefined)?pokemon[0]:""} /></div>
                                <div className='stat'>Stat</div>
                                <div className='speed'>Speed: {(pokemon!==undefined)?pokemon[2][0].base_stat:""}</div>
                                <div className='specialStats'>Special defense: {(pokemon!==undefined)?pokemon[2][1].base_stat:""}<br />Special attack: {(pokemon!==undefined)?pokemon[2][2].base_stat:""}</div>
                                <div className='defenseAttack'>Defense: {(pokemon!==undefined)?pokemon[2][3].base_stat:""}<br />Attack: {(pokemon!==undefined)?pokemon[2][4].base_stat:""}</div>
                                <div className='hp'>HP: {(pokemon!==undefined)?pokemon[2][5].base_stat:""}</div>
                                <div className='type1 typeOfPokemon' id={(pokemon!==undefined)?pokemon[3][0].type.name:""}>{(pokemon!==undefined)?pokemon[3][0].type.name:""}</div>
                                <div className='type2 typeOfPokemon' id={(pokemon!==undefined)?((pokemon[3][1] === undefined) ? '' : pokemon[3][1].type.name):""}>{(pokemon!==undefined)?((pokemon[3][1] === undefined) ? '' : pokemon[3][1].type.name):""}</div>
                                <div className='type3 typeOfPokemon' id={(pokemon!==undefined)?((pokemon[3][2] === undefined) ? '' : pokemon[3][2].type.name):""}>{(pokemon!==undefined)?((pokemon[3][2] === undefined) ? '' : pokemon[3][2].type.name):""}</div>
                            </div>
                        </div>
                        <div className='pokemon-list' id='poki'>{pokemonsOnPage}</div>
                    </div>
                );
            }
        }
    )
)

export default Pokemon;