import React from 'react';
import {inject, observer} from 'mobx-react';
import '../Style/Pagination.css';

let Pagination = inject('Store')(
    observer(
        class Pagination extends React.Component{
            getAllPages = () =>{
                let allPages = [];
                for(let i = 1; i <= 800 / this.props.Store.countPokemonOnPage; i++){
                    allPages[i] = <div className='pagination-value' id={i} onClick =  {()=>this.changefunction(i)}>{i}</div>;
                }
                return allPages;
            }
            changefunction = (value) =>{
                console.log(value);
                this.props.Store.change_clickedPage(value);
            }
            convertToNormalView = (many_pages) =>{
                let normalView = [];
                let click = this.props.Store.clickedPage;
                if(click < 6){
                    for(let i = 1; i<=7; i++){
                        normalView[i] = many_pages[i];
                    }
                    normalView[8] = <div className = 'dots'>...</div>
                    normalView[9] = many_pages[many_pages.length-1];
                    normalView[click] = <div className='pagination-value clicked-page' id={click} onClick =  {()=>this.changefunction(click)}>{click}</div>
                }
                else if(click > many_pages.length-4){
                    normalView[1] = many_pages[1];
                    normalView[2] = <div className = 'dots'>...</div>
                    for(let i = 3, j = many_pages.length - 7; i<=9; i++, j++){
                        normalView[i] = many_pages[j];
                    }
                    normalView[9-((many_pages.length-1) - click)] = <div className='pagination-value clicked-page' id={click} onClick =  {()=>this.changefunction(click)}>{click}</div>
                }
                else{
                    normalView[1] = many_pages[1];
                    normalView[2] = <div className = 'dots'>...</div>
                    for(let i = 3, j = this.props.Store.clickedPage-2; i<=7; i++, j++){
                        normalView[i] = many_pages[j];
                    }
                    normalView[8] = <div className = 'dots'>...</div>
                    normalView[9] = many_pages[many_pages.length-1];
                    normalView[5] = <div className='pagination-value clicked-page' id={click} onClick =  {()=>this.changefunction(click)}>{click}</div>
                }
                return normalView;
            }
            render(){
                let pagination1 = this.convertToNormalView(this.getAllPages());
                return(
                    <div className='pagination-main'>{pagination1}</div>
                );
            }
        }
    )
)

export default Pagination;