import React from 'react';
import {inject, observer} from 'mobx-react';

import PokemonFile from './Pokemon';
import Pagination from './Pagination';
import Filter from './Filter';

import '../Style/Page.css';
import { strict } from 'assert';
import { stringLiteral } from '@babel/types';

let Page = inject('Store')(
    observer(
        class FullPage extends React.Component{
            changeValue = (size) =>{
                this.props.Store.change_countPokemonOnPage(size.target.value);
                this.props.Store.change_clickedPage(1);
            }
            sortPokemons = () =>{
                let pokemons = this.props.Store.allPokemons;
                console.log(pokemons);
                pokemons = pokemons.sort(this.sortFunction);
                this.props.Store.change_allPokemons(pokemons);
                console.log(pokemons);
                let temp = this.props.Store.countPokemonOnPage;
                this.props.Store.change_countPokemonOnPage(20);
                this.props.Store.change_countPokemonOnPage(temp);
            }
            sortFunction = (a, b) =>{
                if(a[1]>b[1]){
                    return 1;
                }
                else if (a[1]<b[1]){
                    return -1;
                }
                return 0;
            }
            refresheAll = () =>{
                this.props.Store.get_allpokemons();
                let types = this.props.Store.typesOfPokemons;
                for(let i = 0; i<types.length; i++){
                    document.getElementById(types[i]).checked = false;
                }
                this.props.Store.change_clickedPage(1);
            }
            searchByValue = (val) =>{
                let temp = this.props.Store.allPokemons;
                console.log(val.target.value.length);
                if(val.target.value.length === 0){
                    this.props.Store.get_allpokemons();
                    return;
                }
                const result = temp.filter(pokemon => {if(pokemon[1].indexOf(val.target.value.toLowerCase())!==-1) return true; return false;});
                this.props.Store.change_allPokemons(result);
            }
            render(){
                return(
                    <div className = 'page-elements'>
                        <h1 className='page-name' onClick={this.refresheAll}>Pokedex</h1>
                        <select className='size-box' onChange = {this.changeValue}>
                            <option value="10">10</option>
                            <option value="20">20</option>
                            <option value="50">50</option>
                        </select>
                        <div className = 'buttons'>
                            <input type = 'search' className = 'searcher' name='q' placeholder="I will help you find" onChange={this.searchByValue}/>
                            <div className='sort' onClick = {this.sortPokemons}>Sort elements</div>
                            <div className='refresh' onClick={this.refresheAll}>Refresh</div>
                            <Filter />
                        </div>
                        <PokemonFile />
                        <Pagination />
                    </div>
                );
            }
        }
    )
)

export default Page;